body {
  min-height: 100vh;
  color: #fff;
  background: #000;
  font-family: PT Sans;
  font-size: 14px;
  line-height: 1.5;
}

a {
  color: #fff;
  white-space: nowrap;
  text-decoration: none;
}

h3 {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 70px;
  font-family: Playfair Display, serif;
  font-size: 36px;
  line-height: 48px;
}

h3:after {
  content: "";
  width: 120px;
  border-bottom: 1px solid #fff;
  display: block;
  position: relative;
  bottom: -20px;
  left: calc(50% - 60px);
}

input, button {
  outline: none;
}

a, button {
  cursor: pointer;
  transition: color .3s cubic-bezier(.4, 0, .2, 1);
}

a:hover, button:hover {
  transition: color .75s, transform .75s;
  transform: scale(1.1);
}

.link {
  text-transform: uppercase;
  letter-spacing: .1em;
  border: 1px solid #fff;
  padding: 17px 28px;
  font-size: 18px;
  display: inline-block;
}

.wrapper {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
}

.img_wrapper {
  width: 200px;
  height: 260px;
  background: #191b1c;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  padding: 27px 22px 34px;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .img_wrapper {
    width: 260px;
    height: 325px;
  }
}

html {
  cursor: url("cursor-black&green.7f32866c.svg"), pointer;
}

a, a:hover {
  cursor: url("cursor-black&green.7f32866c.svg"), pointer;
}

button, button:hover {
  cursor: url("cursor-black&green.7f32866c.svg"), pointer;
}

.active, li.active a {
